.booking-detail-wrapper {
  .payment-method-container {
    height: 34vw;
  }
  p {
    font-family: 'Google Sans', sans-serif;
  }
  display: flex;
  flex-direction: column;
  ul {
    list-style: disc;
  }
  .voucher_name_container {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  .social_container {
    display: flex;
    margin-top: 28px;
    align-items: center;
    margin-bottom: 38px;
  }
  .button_booking_container {
    position: fixed;
    bottom: 0;
    z-index: 1000;
    height: 84px;
    width: 100%;
    background-color: white;
    padding: 18px;
  }
  .button_booking {
    width: 100%;
    height: 100%;
  }
  li {
    color: #3a556a;
    margin-bottom: 10px;
  }
  .merchant_info {
    margin-top: 40px;
    background-color: white;
    padding: 20px;
    border-radius: 4px;
  }
  .merchant_logo {
    border-radius: 40px;
    width: 60px;
    height: 60px;
    filter: drop-shadow(0px 4px 11px rgba(0, 0, 0, 0.25));
  }
  .merchant_name {
    margin-left: 12px;
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
  }
  .merchant_description {
    margin-top: 10px;
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
  }
  .img-logo-home {
    border-radius: 40px;
    width: 80px;
    height: 80px;
    filter: drop-shadow(0px 4px 11px rgba(0, 0, 0, 0.25));
  }
  .voucher_name {
    margin-left: 20px;
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
  }
  .booking-detail-container {
    align-self: center;
    width: 85%;
    padding-top: 1em;

    .merchant_name {
      font-family: 'Google Sans', sans-serif;
      font-style: normal;
      font-size: 17px;
      padding-left: 1em;
    }
    .share_title {
      font-family: 'Google Sans', sans-serif;
      color: #3a3a3c;
      font-weight: 400;
      font-size: 15px;
      padding-right: 12px;
    }
    .social-icons {
      padding-left: 12px;
    }
    .voucher_image {
      height: 430px;
      width: 700px;
    }
    .map_title {
      font-size: 24px;
      font-weight: 900;
      font-family: 'Google Sans', sans-serif;
    }

    .container-left {
      .header-title {
      }
      .tab_title {
        font-size: 24px;
        line-height: 29px;
        font-weight: bold;
        color: red;
        font-family: 'Google Sans', sans-serif;
        border-left: 4px solid #ff8647;
        padding-left: 15px;
        margin-top: 15px;
      }
      .tab_content {
        font-size: 15px;
        line-height: 25px;
        padding: 15px;
        margin-bottom: 20px;
        overflow: hidden;
        border-bottom: 1px solid #ecedf0;
      }
    }
  }
  .promotion-section {
    margin-top: 10px;
    background: white;
    padding: 1em;

    .promotion-title {
      font-size: 24px;
      font-weight: 700;
      font-family: 'Google Sans', sans-serif;
      font-size: 17px;
      padding-bottom: 1.5em;
    }
    .logo-home {
      width: 80px;
      height: 80px;
      border-radius: 50px;
      overflow: hidden;
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
        0 6px 20px 0 rgba(0, 0, 0, 0.19);
      .img-logo-home {
      }
    }
  }
  @media screen and (max-width: 600px) {
    .booking-detail-container {
      width: 100%;
    }
    .slide_image_show {
      height: 180px !important;
    }
    .social_container {
      margin-top: 18px;
      margin-bottom: 18px;
    }
    .voucher_name_container {
      margin-bottom: 0px;
    }
    .img-logo-home {
      border-radius: 30px;
      width: 60px;
      height: 60px;
    }
    .voucher_name {
      font-size: 1.5rem;
    }
  }
  @media screen and (max-width: 960px) {
    .payment-method-container {
      height: auto;
    }
  }

  @media screen and (max-width: 1280px) {
    .payment-method-container {
      height: 450px;
    }
  }
}
