.map_container div:first-child {
  position: relative !important;
}

.list_branch_map_container {
  padding: 20px;
  margin-bottom: 20px;
  .image_merchant_marker {
    width: 44px;
    height: 44px;
    border-radius: 22px;
    margin-right: 10px;
    margin-bottom: 10px;
  }
  .merchant_name_marker {
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 17px;
  }
  .merchant_address_marker {
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
  }
  .title_marker {
    display: flex;
    align-items: center;
  }
  .suggestion_branch_container {
    position: relative;
    display: flex;
    align-items: center;
    border: 1px solid #d5d6db;
    border-radius: 4px;
    padding: 12px 0px;
    margin-bottom: 20px;
  }

  .selected_branch_text {
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    flex: 1;
    margin-left: 16px;
    font-size: 15px;
  }
  .branch_name_text {
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    flex: 1;
    margin: 10px 16px;
    cursor: pointer;
    font-size: 15px;
  }
  .list_branch_modal_container {
    position: absolute;
    top: 55px;
    background-color: white;
    width: 100%;

    max-height: 200px;
    z-index: 2;
    overflow: scroll;
    box-shadow: 0px 4px 11px rgba(0, 0, 0, 0.25);
  }
}
